.container {
  overflow: hidden;
  height: 100%;
  width: 100%; }

.fullpage {
  position: relative;
  -webkit-transition: all 1000ms ease 0s;
  -o-transition: all 1000ms ease 0s;
  transition: all 1000ms ease 0s;
  height: 3935px; }

section {
  width: 100%;
  display: table;
  table-layout: fixed;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 787px; }

.pagination {
  position: fixed;
  top: 50%;
  right: 0.16em;
  z-index: 100;
  opacity: 1;
  margin-top: -0.525rem; }
  .pagination ul, .pagination li {
    list-style: none;
    padding: 0;
    margin: 0; }
  .pagination li {
    width: 0.14rem;
    height: 0.14rem;
    margin: 0.07rem;
    position: relative; }
    .pagination li a {
      cursor: pointer; }
    .pagination li:hover:not(.active) span {
      width: 0.12rem;
      height: 0.12rem;
      margin: -0.06rem -0.06rem; }
    .pagination li a.active span {
      width: 0.12rem;
      height: 0.12rem;
      margin: -0.06rem -0.06rem; }
    .pagination li a span {
      background: #666;
      width: 0.04rem;
      height: 0.04rem;
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -0.02rem -0.02rem;
      border-radius: 100%;
      z-index: 1;
      -webkit-transition: all 0.1s ease-in-out;
      -o-transition: all 0.1s ease-in-out;
      transition: all 0.1s ease-in-out; }



